<template>
  <v-card
    elevation="0"
    :key="item.id"
    class="my-2 py-2 d-flex flex-row rounded"
  >
    <template class="d-flex flex-row align-start">
      <v-img
        :src="getImagePath(imgName, categoryCode)"
        width="78"
        max-width="78"
        v-for="(imgName, index) in item.imgNames"
        :key="index"
        :class="desktop ? 'desktop' : getMobileClass(index)"
        class="rounded-sm"
      ></v-img>
    </template>
    <v-card elevation="0" :class="desktop ? 'ms-5' : 'ms-2'">
      <v-card-header class="pt-0 px-0">
        <v-card-header-text class="pt-0">
          <div class="text-subtitle-2">{{ item.name }}</div>
          <div class="pa-0">
            <template class="me-1 d-inline">
              <v-btn
                class="btn-info"
                color="primary"
                size="x-small"
                variant="outlined"
                @click="openDialog(item, categoryCode, isCard)"
              >
                <v-icon class="ma-0 icon" aria-hidden="false">
                  mdi-information
                </v-icon>
                INFO
              </v-btn>
            </template>

            <v-chip
              class="px-1 py-0 me-1"
              size="x-small"
              variant="outlined"
              label
              :color="getAvailableClass(item.available)"
            >
              {{ getAvailableText(item.available) }}
            </v-chip>

            <span :class="desktop ? 'ms-2' : 'ms-0'" v-if="item.star > 0">
              <span class="rank me-1 font-weight-medium">{{
                item.star.toFixed(1)
              }}</span>
              <v-icon
                icon="mdi-star"
                v-for="n in Math.floor(item.star)"
                :key="n"
                color="orange"
              ></v-icon>
              <v-icon
                icon="mdi-star-half-full"
                v-if="Math.floor(item.star) != Math.ceil(item.star)"
                color="orange"
              ></v-icon>
              <v-icon
                icon="mdi-star-outline"
                v-for="n in 5 - Math.ceil(item.star)"
                :key="n"
                color="orange"
              ></v-icon>
            </span>
          </div>
        </v-card-header-text>
      </v-card-header>

      <v-card-text class="px-0">
        <div v-for="(line, index) in item.comments" :key="index">
          {{ line }}
        </div>
        <div v-if="!isCard" class="text-red mt-5">
          <v-icon size="small" class="mb-1">mdi-treasure-chest</v-icon>
          取得方式：{{ item.loot }}
        </div>
        <template v-if="!isCard">
          <div
            v-for="(link, index) in item.externalUrls"
            :key="index"
            class="text-secondary"
          >
            <v-btn
              variant="plain"
              color="secondary"
              class="py-0 px-0"
              :href="link.url"
              target="_blank"
            >
              <v-icon class="mb-1">mdi-open-in-new</v-icon>
              {{ link.name }}
            </v-btn>
          </div>
        </template>
      </v-card-text>
    </v-card>
  </v-card>
</template>
<script>
import { useDisplay } from 'vuetify';
export default {
  name: 'EquipmentComment',
  data: () => ({}),
  components: {},
  props: ['item', 'categoryCode', 'isCard'],
  mounted() {},
  computed: {
    desktop() {
      console.log(this.displayType());
      return this.displayType() == 1;
    },
    mobile() {
      console.log(this.displayType());
      return this.displayType() == 0;
    },
  },
  methods: {
    getImagePath(imgName, categoryCode) {
      let folder = 'equipment';
      if (categoryCode == 'pet') {
        folder = 'pet';
      }
      if (this.isCard) {
        folder = 'card';
      }
      return `${process.env.BASE_URL}image/${folder}/${imgName}`;
    },
    getAvailableText(available) {
      let text = '';
      if (available) {
        text = '已實裝';
      } else {
        text = '未實裝';
      }
      return text;
    },
    getAvailableClass(available) {
      let availableClass = '';
      if (available) {
        availableClass = 'secondary';
      } else {
        availableClass = 'error';
      }
      return availableClass;
    },
    displayType() {
      const { name } = useDisplay();
      let type = 0;
      switch (name.value) {
        case 'xs':
        case 'sm':
        case 'md':
          type = 0;
          break;
        case 'lg':
        case 'xl':
        case 'xxl':
          type = 1;
          this.drawer = true;
          break;
        default:
          break;
      }
      return type;
    },
    getMobileClass(index) {
      if (index == 0) {
        return `mobile-1`;
      } else {
        return `mobile mobile-${index + 1}`;
      }
    },
    openDialog(item, categoryCode, isCard) {
      this.$emit('openDialog', item, categoryCode, isCard);
    },
  },
};
</script>
<style scope>
.rank {
  padding-top: 1px;
}
.mobile {
  margin-left: -50px !important;
}
@media (max-width: 530px) {
  .mobile {
    margin-left: -78px !important;
  }
}

.mobile-1 {
  z-index: 6;
}
.mobile-2 {
  z-index: 5;
}
.mobile-3 {
  z-index: 4;
}
.mobile-4 {
  z-index: 3;
}
.mobile-5 {
  z-index: 2;
}
.mobile-6 {
  z-index: 1;
}
</style>
